import React, { useState, useEffect } from 'react';
import axios from 'axios';
import toast from 'react-hot-toast';
import { motion, AnimatePresence } from 'framer-motion';
import { FiEdit2, FiTrash2 } from 'react-icons/fi';
import TeamAccordion from './TeamAccordion';
import HostControls from './HostControls';

const Leaderboard = ({ data, hostMode, updateData, theme }) => {
  const [expandedTeamId, setExpandedTeamId] = useState(null);
  const [sortedTeams, setSortedTeams] = useState([]);
  const [editingDayId, setEditingDayId] = useState(null);
  const [editingDayName, setEditingDayName] = useState('');
  const [editingDayDate, setEditingDayDate] = useState('');
  const [editingQuizId, setEditingQuizId] = useState(null);
  const [editingQuizName, setEditingQuizName] = useState('');
  const [editingTeamId, setEditingTeamId] = useState(null);
  const [editingTeamName, setEditingTeamName] = useState('');

  useEffect(() => {
    const teamsWithAverages = data.teams.map(team => {
      const teamMemberIds = team.teammates.map(m => m.id);
      const teamScores = data.scores.filter(score => teamMemberIds.includes(score.memberId));
      const totalScore = teamScores.reduce((sum, score) => sum + score.score, 0);
      const average = teamScores.length > 0 ? totalScore / teamScores.length : 0;
      return { ...team, average: Math.round(average * 100) / 100, totalScore };
    });
    setSortedTeams(teamsWithAverages.sort((a, b) => b.average - a.average));
  }, [data]);

  const toggleTeamExpansion = (teamId) => {
    if (expandedTeamId === teamId) {
      setExpandedTeamId(null);
    } else {
      setExpandedTeamId(teamId);
    }
  };

  const getScoreForRound = (teamId, roundId) => {
    // For team score, average of member scores
    const teamMemberIds = data.teams.find(t => t.id === teamId)?.teammates.map(m => m.id) || [];
    const memberScores = data.scores.filter(s => teamMemberIds.includes(s.memberId) && s.roundId === roundId);
    if (memberScores.length === 0) return '-';
    const average = memberScores.reduce((sum, s) => sum + s.score, 0) / memberScores.length;
    return Math.round(average * 100) / 100;
  };

  const updateScore = async (teamId, roundId, newScore) => {
    try {
      const teamMemberIds = data.teams.find(t => t.id === teamId)?.teammates.map(m => m.id) || [];
      const existingScores = data.scores.filter(s => teamMemberIds.includes(s.memberId) && s.roundId === roundId);
      
      if (existingScores.length > 0) {
        // Update all member scores for this round to the same value
        await Promise.all(existingScores.map(score => 
          axios.patch(`http://localhost:3001/scores/${score.id}`, { score: parseInt(newScore) })
        ));
        updateData({
          ...data,
          scores: data.scores.map(score =>
            existingScores.some(es => es.id === score.id) 
              ? { ...score, score: parseInt(newScore) } 
              : score
          )
        });
      } else {
        // Create new scores for each team member
        const newScores = await Promise.all(teamMemberIds.map(memberId =>
          axios.post('http://localhost:3001/scores', {
            memberId,
            roundId,
            score: parseInt(newScore)
          })
        ));
        updateData({ ...data, scores: [...data.scores, ...newScores.map(res => res.data)] });
      }
      toast.success('Score updated successfully');
    } catch (error) {
      console.error('Error updating score:', error);
      toast.error('Failed to update score');
    }
  };

  const addTeam = async (teamName, teamMembers) => {
    try {
      const newTeam = { name: teamName, teammates: teamMembers };
      const res = await axios.post('http://localhost:3001/teams', newTeam);
      updateData({ ...data, teams: [...data.teams, res.data] });
      toast.success(`Team "${teamName}" added successfully`);
    } catch (error) {
      console.error('Error adding team:', error);
      toast.error('Failed to add team');
    }
  };

  const addQuiz = async (quizName, dayId) => {
    try {
      const newQuiz = { name: quizName, dayId };
      const res = await axios.post('http://localhost:3001/quizzes', newQuiz);
      const newRound = { name: 'Round 1', quizId: res.data.id };
      await axios.post('http://localhost:3001/rounds', newRound);
      // Refresh data
      const [quizzesRes, roundsRes] = await Promise.all([
        axios.get('http://localhost:3001/quizzes'),
        axios.get('http://localhost:3001/rounds'),
      ]);
      updateData({ ...data, quizzes: quizzesRes.data, rounds: roundsRes.data });
      toast.success(`Quiz "${quizName}" added successfully`);
    } catch (error) {
      console.error('Error adding quiz:', error);
      toast.error('Failed to add quiz');
    }
  };

  const addDay = async (dayName, dayDate) => {
    try {
      const res = await axios.post('http://localhost:3001/days', { name: dayName, date: dayDate });
      updateData({ ...data, days: [...data.days, res.data] });
      toast.success(`Day "${dayName}" added successfully`);
    } catch (error) {
      console.error('Error adding day:', error);
      toast.error('Failed to add day');
    }
  };

  const updateDay = async (dayId, newName, newDate) => {
    try {
      await axios.patch(`http://localhost:3001/days/${dayId}`, { name: newName, date: newDate });
      updateData({
        ...data,
        days: data.days.map(day => day.id === dayId ? { ...day, name: newName, date: newDate } : day)
      });
      setEditingDayId(null);
      toast.success(`Day updated: ${newName} ${newDate ? `(${newDate})` : ''}`);
    } catch (error) {
      console.error('Error updating day:', error);
      toast.error('Failed to update day');
    }
  };

  const deleteDay = async (dayId) => {
    if (!window.confirm('Are you sure you want to delete this day? This will also delete all associated quizzes and scores.')) return;
    try {
      // Delete associated quizzes
      const quizzesToDelete = data.quizzes.filter(q => q.dayId === dayId);
      await Promise.all(quizzesToDelete.map(q => axios.delete(`http://localhost:3001/quizzes/${q.id}`)));
      
      // Delete associated scores
      const roundsToDelete = data.rounds.filter(r => quizzesToDelete.some(q => q.id === r.quizId));
      const scoresToDelete = data.scores.filter(s => roundsToDelete.some(r => r.id === s.roundId));
      await Promise.all(scoresToDelete.map(s => axios.delete(`http://localhost:3001/scores/${s.id}`)));
      
      // Delete rounds
      await Promise.all(roundsToDelete.map(r => axios.delete(`http://localhost:3001/rounds/${r.id}`)));
      
      // Delete day
      await axios.delete(`http://localhost:3001/days/${dayId}`);
      
      updateData({
        ...data,
        days: data.days.filter(d => d.id !== dayId),
        quizzes: data.quizzes.filter(q => q.dayId !== dayId),
        rounds: data.rounds.filter(r => !roundsToDelete.some(rt => rt.id === r.id)),
        scores: data.scores.filter(s => !scoresToDelete.some(st => st.id === s.id))
      });
      toast.success('Day deleted successfully');
    } catch (error) {
      console.error('Error deleting day:', error);
      toast.error('Failed to delete day');
    }
  };

  const updateQuiz = async (quizId, newName) => {
    try {
      await axios.patch(`http://localhost:3001/quizzes/${quizId}`, { name: newName });
      updateData({
        ...data,
        quizzes: data.quizzes.map(quiz => quiz.id === quizId ? { ...quiz, name: newName } : quiz)
      });
      setEditingQuizId(null);
      toast.success(`Quiz updated: ${newName}`);
    } catch (error) {
      console.error('Error updating quiz:', error);
      toast.error('Failed to update quiz');
    }
  };

  const updateTeamNameAsync = async (teamId, newName) => {
    try {
      await axios.patch(`http://localhost:3001/teams/${teamId}`, { name: newName });
      updateData({
        ...data,
        teams: data.teams.map(team => team.id === teamId ? { ...team, name: newName } : team)
      });
      setEditingTeamId(null);
      toast.success(`Team updated: ${newName}`);
    } catch (error) {
      console.error('Error updating team:', error);
      toast.error('Failed to update team');
    }
  };

  const deleteTeam = async (teamId) => {
    if (!window.confirm('Are you sure you want to delete this team?')) return;
    try {
      await axios.delete(`http://localhost:3001/teams/${teamId}`);
      // Also delete related scores
      const teamMemberIds = data.teams.find(t => t.id === teamId)?.teammates.map(m => m.id) || [];
      const scoresToDelete = data.scores.filter(s => teamMemberIds.includes(s.memberId));
      await Promise.all(scoresToDelete.map(s => axios.delete(`http://localhost:3001/scores/${s.id}`)));
      updateData({
        ...data,
        teams: data.teams.filter(team => team.id !== teamId),
        scores: data.scores.filter(s => !teamMemberIds.includes(s.memberId))
      });
      toast.success('Team deleted successfully');
    } catch (error) {
      console.error('Error deleting team:', error);
      toast.error('Failed to delete team');
    }
  };

  return (
    <div className="overflow-x-auto">
      {hostMode && <HostControls addTeam={addTeam} addQuiz={addQuiz} addDay={addDay} days={data.days} theme={theme} data={data} updateData={updateData} />}
      <motion.table
        initial={{ opacity: 0 }}
        animate={{ opacity: 1 }}
        transition={{ duration: 0.3 }}
        className={`w-full table-auto border ${theme.colors.border}`}
      >
        <thead>
          <tr className={`border-b ${theme.colors.border}`}>
            <th className={`px-6 py-3 text-left text-xs uppercase tracking-wider ${theme.colors.muted}`}>Rank</th>
            <th className={`px-6 py-3 text-left text-xs uppercase tracking-wider ${theme.colors.muted}`}>Team</th>
            <th className={`px-6 py-3 text-left text-xs uppercase tracking-wider ${theme.colors.muted}`}>Average</th>
            <th className={`px-6 py-3 text-left text-xs uppercase tracking-wider ${theme.colors.muted}`}>Total</th>
            {data.days.map(day => (
              <th key={day.id} colSpan={data.quizzes.filter(q => q.dayId === day.id).length} className={`px-6 py-3 text-center border-l ${theme.colors.border} text-xs uppercase tracking-wider ${theme.colors.muted}`}>
                <div className="flex items-center justify-center space-x-2">
                  {editingDayId === day.id ? (
                    <div className="flex flex-col space-y-1">
                      <input
                        type="text"
                        value={editingDayName}
                        onChange={(e) => setEditingDayName(e.target.value)}
                        className={`px-2 py-1 text-xs ${theme.colors.input} border ${theme.colors.border} rounded`}
                      />
                      <input
                        type="date"
                        value={editingDayDate}
                        onChange={(e) => setEditingDayDate(e.target.value)}
                        className={`px-2 py-1 text-xs ${theme.colors.input} border ${theme.colors.border} rounded`}
                      />
                      <div className="flex space-x-1 justify-center">
                        <button
                          onClick={() => updateDay(day.id, editingDayName, editingDayDate)}
                          className="px-2 py-1 bg-green-600 hover:bg-green-700 text-white text-xs rounded"
                        >
                          
                        </button>
                        <button
                          onClick={() => setEditingDayId(null)}
                          className="px-2 py-1 bg-red-600 hover:bg-red-700 text-white text-xs rounded"
                        >
                          
                        </button>
                      </div>
                    </div>
                  ) : (
                    <>
                      <span>{day.name} {day.date && `(${day.date})`}</span>
                      {hostMode && (
                        <div className="flex space-x-1">
                          <button
                            onClick={() => {
                              setEditingDayId(day.id);
                              setEditingDayName(day.name);
                              setEditingDayDate(day.date || '');
                            }}
                            title="Edit day"
                            className={`p-2 text-lg hover:opacity-80 transition-opacity`}
                          >
                            <FiEdit2 size={18} />
                          </button>
                          <button
                            onClick={() => deleteDay(day.id)}
                            title="Delete day"
                            className={`p-2 text-lg hover:opacity-80 transition-opacity text-red-500`}
                          >
                            <FiTrash2 size={18} />
                          </button>
                        </div>
                      )}
                    </>
                  )}
                </div>
              </th>
            ))}
          </tr>
          <tr className={`border-b ${theme.colors.border}`}>
            <th></th>
            <th></th>
            <th></th>
            <th></th>
            {data.days.map(day =>
              data.quizzes.filter(q => q.dayId === day.id).map(quiz => (
                <th key={quiz.id} className={`px-6 py-3 text-center border-l ${theme.colors.border} text-xs ${theme.colors.muted}`}>
                  <div className="flex items-center justify-center space-x-2">
                    {editingQuizId === quiz.id ? (
                      <div className="flex flex-col space-y-1">
                        <input
                          type="text"
                          value={editingQuizName}
                          onChange={(e) => setEditingQuizName(e.target.value)}
                          className={`px-2 py-1 text-xs ${theme.colors.input} border ${theme.colors.border} rounded`}
                        />
                        <div className="flex space-x-1 justify-center">
                          <button
                            onClick={() => updateQuiz(quiz.id, editingQuizName)}
                            className="px-2 py-1 bg-green-600 hover:bg-green-700 text-white text-xs rounded"
                          >
                            
                          </button>
                          <button
                            onClick={() => setEditingQuizId(null)}
                            className="px-2 py-1 bg-red-600 hover:bg-red-700 text-white text-xs rounded"
                          >
                            
                          </button>
                        </div>
                      </div>
                    ) : (
                      <>
                        <span>{quiz.name}</span>
                        {hostMode && (
                          <button
                            onClick={() => {
                              setEditingQuizId(quiz.id);
                              setEditingQuizName(quiz.name);
                            }}
                            title="Edit quiz"
                            className={`p-2 text-lg hover:opacity-80 transition-opacity`}
                          >
                            <FiEdit2 size={18} />
                          </button>
                        )}
                      </>
                    )}
                  </div>
                </th>
              ))
            )}
          </tr>
        </thead>
        <tbody>
          <AnimatePresence>
            {sortedTeams.map((team, index) => (
              <React.Fragment key={team.id}>
                <motion.tr
                  initial={{ opacity: 0, y: 20 }}
                  animate={{ opacity: 1, y: 0 }}
                  exit={{ opacity: 0, y: -20 }}
                  transition={{ duration: 0.3, delay: index * 0.1 }}
                  className={`border-b ${theme.colors.border} hover:bg-opacity-50 cursor-pointer`}
                  onClick={() => toggleTeamExpansion(team.id)}
                >
                  <td className="px-4 py-2">{index + 1}</td>
                  <td className="px-4 py-2 flex items-center">
                    <img src={team.teammates[0]?.avatar} alt="avatar" className="w-8 h-8 rounded-full mr-2" />
                    {editingTeamId === team.id ? (
                      <div className="flex items-center space-x-2">
                        <input
                          type="text"
                          value={editingTeamName}
                          onChange={(e) => setEditingTeamName(e.target.value)}
                          className={`flex-1 px-2 py-1 text-xs ${theme.colors.input} border ${theme.colors.border} rounded`}
                          onClick={(e) => e.stopPropagation()}
                        />
                        <button
                          onClick={(e) => {
                            e.stopPropagation();
                            updateTeamNameAsync(team.id, editingTeamName);
                          }}
                          className="px-2 py-1 bg-green-600 hover:bg-green-700 text-white text-xs rounded"
                        >
                          
                        </button>
                        <button
                          onClick={(e) => {
                            e.stopPropagation();
                            setEditingTeamId(null);
                          }}
                          className="px-2 py-1 bg-red-600 hover:bg-red-700 text-white text-xs rounded"
                        >
                          
                        </button>
                      </div>
                    ) : (
                      <>
                        <span className="flex-1">{team.name}</span>
                        {hostMode && (
                          <button
                            onClick={(e) => {
                              e.stopPropagation();
                              setEditingTeamId(team.id);
                              setEditingTeamName(team.name);
                            }}
                            title="Edit team"
                            className={`p-2 text-lg hover:opacity-80 transition-opacity mr-2`}
                          >
                            <FiEdit2 size={18} />
                          </button>
                        )}
                        {hostMode && (
                          <button
                            onClick={(e) => { e.stopPropagation(); deleteTeam(team.id); }}
                            title="Delete team"
                            className={`p-2 text-lg hover:opacity-80 transition-opacity ml-4 text-red-500`}
                          >
                            <FiTrash2 size={18} />
                          </button>
                        )}
                      </>
                    )}
                  </td>
                  <td className="px-4 py-2 font-bold text-yellow-400">{team.average}</td>
                  <td className="px-4 py-2 font-bold text-yellow-400">{team.totalScore}</td>
                  {data.rounds.map(round => (
                    <td key={round.id} className="px-4 py-2 text-center border-l border-gray-600">
                      {hostMode ? (
                        <input
                          type="number"
                          min="0"
                          step="1"
                          value={getScoreForRound(team.id, round.id) === '-' ? '' : getScoreForRound(team.id, round.id)}
                          onChange={(e) => updateScore(team.id, round.id, parseInt(e.target.value) || 0)}
                          className={`w-24 py-2 px-3 text-sm ${theme.colors.input} border ${theme.colors.border} rounded text-center font-semibold`}
                          onClick={(e) => e.stopPropagation()}
                        />
                      ) : (
                        getScoreForRound(team.id, round.id)
                      )}
                    </td>
                  ))}
                </motion.tr>
                <AnimatePresence>
                  {expandedTeamId === team.id && (
                    <motion.tr
                      initial={{ opacity: 0, height: 0 }}
                      animate={{ opacity: 1, height: 'auto' }}
                      exit={{ opacity: 0, height: 0 }}
                      transition={{ duration: 0.3 }}
                    >
                      <td colSpan={4 + data.rounds.length} className="px-4 py-2">
                        <TeamAccordion team={team} hostMode={hostMode} updateData={updateData} theme={theme} />
                      </td>
                    </motion.tr>
                  )}
                </AnimatePresence>
              </React.Fragment>
            ))}
          </AnimatePresence>
        </tbody>
      </motion.table>
    </div>
  );
};

export default Leaderboard;
